<template>
  <div class="userInfo-div">
    <el-card class="box-card_title">
      <div class="welcome_div">
        <div class="text item">我的资料</div>
        <div class="welcome">以下是您的资料卡，欢迎完善或更改信息！</div>
      </div>
    </el-card>
    <el-card class="box-card userInfo-card">
      <div class="userInfo-box">
        <div class="userInfo-img">
          <el-upload
            :http-request="upLoad"
            :show-file-list="false"
            class="avatar-uploader"
            :on-success="handleAvatarSuccess"
            name="file"
            :action="uploadUrl"
            :before-upload="beforeAvatarUpload"
          >
            <img
            class="headimg-css"
              v-if="userInfo.headimg"
              :src="userInfo.headimg"
            />
            <el-avatar
              style="font-size: 38px"
              :size="100"
              v-if="!userInfo.headimg"
              :src="userInfo.headimg"
              >{{ userInfo.username.charAt(0) }}</el-avatar
            >
          </el-upload>
          <!-- <el-avatar :size="100" v-if="userInfo.headimg" :src="userInfo.headimg"></el-avatar>
          <el-avatar style="font-size: 38px;"  :size="100" v-if="!userInfo.headimg" :src="userInfo.headimg">{{ userInfo.username.charAt(0) }}</el-avatar> -->
        </div>
        <el-form
          :model="userInfo"
          :rules="rules"
          ref="updateForm"
          class="demo-ruleForm"
        >
          <el-form-item prop="username">
            <div class="input-css" style="width: 300px">
              <span style="width: 60px">用户名 </span>
              <el-input
                v-model="userInfo.username"
                style="width: 80%"
                placeholder="Username"
              ></el-input>
            </div>
          </el-form-item>
          <el-form-item prop="email">
            <div class="input-css" style="width: 300px">
              邮&nbsp;&nbsp;&nbsp;箱
              <el-input
                :value="userInfo.email"
                style="width: 80%"
                disabled
                placeholder="Email"
              ></el-input>
            </div>
          </el-form-item>
          <el-form-item prop="password">
            <div class="input-css" style="width: 300px">
              密&nbsp;&nbsp;&nbsp;码
              <el-input
                :value="userInfo.password"
                style="width: 80%"
                disabled
                placeholder="Password"
              ></el-input>
            </div>
          </el-form-item>
          <el-form-item prop="phonenumber">
            <div class="input-css" style="width: 300px">
              手机号
              <el-input
                v-model="userInfo.phonenumber"
                style="width: 80%"
                placeholder="PhoneNum"
              ></el-input>
            </div>
          </el-form-item>
          <el-form-item prop="createtime">
            <div class="input-css" style="width: 300px">
              注册时间
              <span class="createtime-span"> {{ userInfo.createtime }} </span>
            </div>
          </el-form-item>
          <div class="button-css">
            <el-button class="login-button-home" @click="gotoMain" type="info"
              >返回首页</el-button
            >
            <el-button class="login-button" @click="updateInfo" type="info"
              >修改信息</el-button
            >
          </div>
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    var checkname = async (rule, value, callback) => {
      if (value != "") {
        const result = await this.checkUserName();
        if (result) {
          callback(new Error("用户名已存在，请重新输入！"));
        } else {
          callback();
        }
      }
      callback();
    };
    return {
      uploadUrl: "",
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 2,
            max: 20,
            message: "长度在 2 到 20 个字符",
            trigger: ["blur", "change"],
          },
          { validator: checkname, trigger: "blur" },
        ],
        phonenumber: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          {
            required: true,
            pattern: /^[1][3,4,5,7,8,9][0-9]{9}$/,
            message: "请输入正确的手机号",
            trigger: "blur",
          },
        ],
      },
      userInfo: {
        headimg: "",
        username: "",
        phonenumber: "",
      },
    };
  },
  methods: {
    gotoMain() {
      this.$store.dispatch("asyncUpdatedActiveIndex","/main")
      this.$router.push("/main");
    },
    async checkUserName() {
      // 如果用户名存在返回true，不存在返回false
      const res = await this.$api.user.checkUserName({
        username: this.userInfo.username,
      });
      if (res.data.code === 200) {
        return false;
      } else {
        return true;
      }
    },

    async updateInfo() {
      this.$refs.updateForm.validate(async (valid) => {
        if (valid) {
          const res = await this.$api.user.updateNameAndPhone(this.userInfo);
          if (res.data.code === 200) {
            this.$message.success("修改成功！");
            this.userInfo = res.data.data;
            window.sessionStorage.setItem(
              "userInfo",
              JSON.stringify(res.data.data)
            );
          } else {
            this.$message.error(res.data.msg);
          }
        }
      });
    },
    beforeAvatarUpload(file) {
      const isJPG =
        file.type == "image/jpeg" ||
        file.type == "image/png" ||
        file.type == "image/gif";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG/PNG/GIF 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    upLoad(file) {
      const formData = new FormData(); //通过form数据格式来传
      formData.append("file", file.file); //传文件
      this.$api.user.uploadUserImage(formData).then((res) => {
        console.log(res);
        console.log("上传成功");
        if (res.data.code === 200) {
          this.$message.success(res.data.msg);
          this.userInfo.headimg = res.data.data;
          //   this.userInfoForm.avatar = this.fileServerUrl() + res.data.fileSrc;
          //   const previewImage = this.userInfoForm.avatar;
          //   console.log("previewImage", previewImage);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    handleAvatarSuccess() {},
  },
  mounted() {
    this.userInfo = JSON.parse(window.sessionStorage.getItem("userInfo"));
    this.$store.dispatch("asyncUpdatedActiveIndex","/user/userInfo")
  },
};
</script>

<style>
.userInfo-div .el-card__body {
  padding: 20px;
}
</style>
<style scoped>
.headimg-css{
    width: 100px;
    height: 100px;
    border-radius: 50px;
}
.createtime-span {
  margin-left: 20px;
}
.userInfo-div {
  margin-top: 70px;
  margin-bottom: 30px;
}
.box-card_title {
  text-align: center;
  margin-bottom: 18px;
}
.userInfo-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.welcome_div {
  margin: 18px;
}
.item {
  font-size: 20px;
  margin-bottom: 20px;
}
.item span {
  border-bottom: #303133 2px solid;
  margin: 0 1px 0 1px;
  display: inline-block;
  height: 30px;
}
.button-css {
  margin-top: -10px;
  margin-bottom: 50px;
}
.userInfo-img {
  margin-top: 20px;
  margin-bottom: 20px;
}
.login-button {
  width: 140px;
  background-color: #eeeeee;
  border: solid 1px #dcdfe6;
  color: #666666;
}
.login-button-home {
  margin-top: 10px;
  width: 140px;
  background-color: #eeeeee;
  border: solid 1px #dcdfe6;
  color: #666666;
}
.login-button-home:hover {
  background-color: #e9b5fa;
  border: solid 1px #e9b5fa;
  color: white;
}
.login-button:hover {
  background-color: #e9b5fa;
  border: solid 1px #e9b5fa;
  color: white;
}
</style>